<template>
  <el-dialog :title="header" width="60%" :visible.sync="dialogVisible" :before-close="cancle">
    <div class="dialog-content">
      <div class="list">
        <span>角色姓名：</span>
        <el-input class="com-input" placeholder="请输入角色姓名" v-model="userName" />
      </div>
      <div class="list">
        <span>角色状态：</span>
        <el-input class="com-input" placeholder="请输入角色状态" v-model="state" />
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancle">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  name: "systemRoleadd",
  props: ["header"],
  data() {
    return {
      dialogVisible: true,
      userName:'',
      state:'',
    };
  },
  methods: {
    cancle(){
      this.$emit('dialogState', {state: false});
    },
    confirm(){
      this.$emit('dialogState', {state: false});
    }
  }
};
</script>
<style lang="less" scoped>
.dialog-content{
  .el-input{
    width: 300px;
  }
  .list{
    display: flex;
    height: 40px;
    line-height: 40px;
    margin-bottom: 20px;
    span{
      display: inline-block;
      min-width: 120px;
      text-align: right;
    }
  }
}
</style>